import React from 'react';
import { render, screen } from '@testing-library/react';
import Hero from '../src/containers/Home/Hero';
import HomeProducts from '../src/containers/Home/HomeProducts';
import Slider from '../src/containers/Home/Slider';

test('Home is rendered correctly', () => {
  render(<Hero />);
  const heroEle = screen.getByText(/Spring Look/i);
  expect(heroEle).toBeInTheDocument();
});

test('HomeProducts is rendered correctly', () => {
  render(<HomeProducts />);
  const HomeProduct = screen.getByTestId('homeProducts');
  expect(HomeProduct).toBeInTheDocument();
});

test('Slider is rendered correctly', () => {
  render(<Slider />);
  const sliderEle = screen.getByTestId('slider');
  expect(sliderEle).toBeInTheDocument();
});